/* eslint-disable no-unused-expressions */
/* eslint-disable @typescript-eslint/no-explicit-any */
import ServiceEngagement from '@/service/ServiceEngagement/ServiceEngagement'
import { defineComponent, ref, onBeforeMount, defineAsyncComponent } from 'vue'
import VueApexCharts from 'vue3-apexcharts'
import VPagination from '@hennge/vue3-pagination'
const SpeakerStatusTracker = defineAsyncComponent(() => import('../Chart/SpeakerStatusTracker/SpeakerStatusTracker.vue'))
const EngagementByStatus = defineAsyncComponent(() => import('../Chart/EngagementByStatus/EngagementByStatus.vue'))

export default defineComponent({
  name: 'DrawerTable',
  components: {
    apexchart: VueApexCharts,
    'spk-status-tracker': SpeakerStatusTracker,
    'eng-by-status': EngagementByStatus,
    VPagination
  },
  props: {
    selectedChartData: {
      default: ''
    },
    speakerprofile: {
      type: String,
      default: ''
    },
    showChart: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      // Speaker Engagement Status Tracker End
      filters: {
        name: { value: '', keys: ['name'] }
      }
    }
  },
  setup (props, { emit }) {
    const selectedChartData = ref(props.selectedChartData) as any
    const loader = ref(false)
    const tableColumn = ref([])
    const tableRow = ref([])
    const currentPage = ref(1) as any
    const totalPages = ref(1) as any
    const searchById = ref('')
    const searchEnable = ref(false)
    const filterEnable = ref(false)
    const filterKey = ref(1)
    const hidefilter = ref(false)
    const engFilter = ref([])
    const filterParam = ref([]) as any
    const filterCount = ref() as any
    const isApplyFilter = ref(false)
    const chartTableList = async (from : any = '') => {
      loader.value = true
      const url = selectedChartData.value.tabelEndpoint
      const postData = selectedChartData.value.postData as any
      if (selectedChartData.value.selectedLabel) {
        postData.label = selectedChartData.value.selectedLabel
      } else if (selectedChartData.value.selectedType) {
        // selectedType
        postData.type = selectedChartData.value.selectedType
      }
      postData.page = currentPage.value
      postData.limit = 10
      if (from === 'search') {
        postData.search = searchById.value
      } else {
        delete postData.search
      }
      if (filterParam && Array.isArray(filterParam.value) && filterParam.value.length !== 0) {
        for (let index = 0; index < filterParam.value.length; index++) {
          const element = filterParam.value[index] as any
          if (element.chooseId.length !== 0) {
            postData[element.filterKey] = element.chooseId
          }
        }
      }
      console.log('-----selectedChartData-----', selectedChartData.value)
      console.log('-----postData-----', postData)
      const res = await ServiceEngagement.commonEndpoint('post', url, postData)
      console.log('----- Chart Table List -------', res)
      if (res.apiStatus && res.data && res.data.rows && res.data.columns) {
        tableColumn.value = res.data.columns && Array.isArray(res.data.columns) ? res.data.columns : []
        tableRow.value = res.data.rows && Array.isArray(res.data.rows) ? res.data.rows : []
      }
      totalPages.value = res.data.totalItems ? res.data.totalItems : 0
      searchEnable.value = res.data.chartTableSearch
      filterEnable.value = res.data.chartTableFilter
      console.log('----- tableRow -------', tableRow.value)
      console.log('----- tableColumn -------', tableColumn.value)
      loader.value = false
    }
    onBeforeMount(async () => {
      try {
        engFilter.value = selectedChartData.value.tableFilterData
        await chartTableList()
        const tableScroll = document.getElementById('tablelist')
        console.log('tableScroll', engFilter.value)
        if (tableScroll) {
          window.scrollTo(0, document.body.scrollHeight)
        }
      } catch (error) {
        console.log('----List Tabel Error ----', error)
      }
    })
    const getTableData = async (chartValue: any) => {
      selectedChartData.value = chartValue
      await chartTableList()
      emit('chartData', selectedChartData.value)
    }
    // Clear Search
    const clearSearch = async () => {
      searchById.value = ''
      isApplyFilter.value = false
      chartTableList()
    }
    window.addEventListener('keypress', function (e: any) {
      if (
        e.key === 'Enter' &&
        (searchById.value !== '')
      ) {
        isApplyFilter.value = true
        currentPage.value = 1
        chartTableList('search')
        return false
      }
    })
    return {
      chartTableList,
      tableColumn,
      tableRow,
      getTableData,
      loader,
      currentPage,
      totalPages,
      searchById,
      searchEnable,
      filterEnable,
      clearSearch,
      filterKey,
      hidefilter,
      engFilter,
      filterParam,
      filterCount,
      isApplyFilter
    }
  },
  methods: {
    // Show/Hide Filter Function
    showfilter () {
      this.filterKey += 1
      this.hidefilter = !this.hidefilter
    },
    // Apply filter Count
    applyFilter (filterData: any) {
      console.log('applyFilter', filterData)
      this.filterParam = []
      if (filterData.type === 'apply') {
        this.filterParam = filterData.array
        this.filterCount = this.filterParam.filter(
          (a: any) => a.chooseId && a.chooseId.length !== 0
        )
      } else {
        this.filterParam = []
      }
      this.chartTableList()
      this.hidefilter = !this.hidefilter
    }
  }
})
